[
`
let obj = {
    a: 'aa'
}
obj.hasOwnProperty('a')
`,
`
function Fn(){}
let fn = new Fn();
Fn.prototype.isPrototypeOf(fn);
`,
`
let obj = Object.create(Object.prototype, {
    a: {
        value: 'aa',
        enumerable: true
    },
    b: {
        value: 'bb',
        enumerable: false
    }
});
obj.propertyIsEnumerable('a');
obj.propertyIsEnumerable('b');
`,
`
Object.assign({}, 'abc');
Object.assign({a: 'aa', b: 'bb'}, {b: 'cc'});
`,
`
const obj = {};
Object.defineProperty(obj, 'a', {
    value: 'aa', // 设置属性的值
    configurable: false, // 设置为false表示不能删除和重新定义
    enumerable: false, // 设置为false表示不能遍历
    writable: false // 设置为false表示不能重新设置值
})
Object.defineProperty(obj, 'b', {
    value: 'aa',
    configurable: true,
    enumerable: true,
    writable: true
})
Object.defineProperty(obj, 'c', {
    enumerable: true,
    get() {
        return 'cc';
    },
    set(value) {
        console.log("设置c: " + value);
    }
})

delete obj.a; // 无效
obj.c="cc"
`,
`
const obj = {};
Object.defineProperties(obj, {
    'a': {
        value: 'aa'
    },
    'b': {
        value: 'bb'
    }
})
`,
`
let parent = {
    a: 'aa'
};
let child  = Object.create(parent, {
    b: {
        value: 'bb'
    }
})
console.log(child.a)
`,
`
const obj = {a: 'aa'};
Object.entries(obj);
`,
`
const obj = {
    a: 'aa',
	b: {
        c: "cc"
    }
};

Object.freeze(obj);
obj.a = 'aaa';
obj.b.c = 'dd';
console.log(obj.a);
console.log(obj.b.c);
`,
`
let obj = {
    a: 'aa'
};

Object.seal(obj);
obj.a = 'aaa';
obj['b'] = 'bb';
console.log(obj);
`,
`
Object.fromEntries(['a', 'b', 'c'].entries())
`,
`
let obj = {a: "aa"};
Object.getOwnPropertyDescriptor(obj, 'a');
`,
`
{
    value: "aa", 
    writable: true, 
    enumerable: true, 
    configurable: true
}
`,
`
let obj = {a: "aa"};
Object.create(
    Object.prototype, 
    Object.getOwnPropertyDescriptors(obj)
);
`,
`
let obj = {a: "aa", [Symbol('b')]: 'bb'};
Object.getOwnPropertyNames(obj);
`,
`
let obj = {a: "aa", [Symbol('b')]: 'bb'};
Object.getOwnPropertySymbols(obj);
`,
`
const parent = {};
let obj = Object.create(parent);
parent === Object.getPrototypeOf(obj);
`,
`
Object.is(Number.NaN, Number.NaN);
Object.is(+0, -0);

Number.NaN === Number.NaN;
+0 === -0;
`,
`
let obj = {};
Object.freeze(obj);
Object.isExtensible(obj);
`,
`
let obj = {};
Object.freeze(obj);
Object.isFrozen(obj);
`,
`
let obj = {};
Object.freeze(obj);
Object.isSealed(obj);
`,
`
const obj  = Object.create({}, {
    a: {
        value: 'aa'
    },
    b: {
        value: 'bb',
        enumerable: true
    }
});
Object.keys(obj);
`,
`
const obj  = Object.create({}, {
    a: {
        value: 'aa'
    },
    b: {
        value: 'bb',
        enumerable: true
    }
});
Object.values(obj);
`,
`
let obj = {};
Object.setPrototypeOf(obj, {a: 'aa'});
console.log(obj.a);
`,
`
let obj = {};
Object.preventExtensions(obj); 
Object.setPrototypeOf(obj, {});
`
